<template>
    <div class="wrapper">
      <div class="inner-wrapper" :style="{maxWidth:$route.meta.fullScreen?'':'1200px'}">
        <ul class="nav left">
          <li>
            <router-link :to="'/home'"
            ><img src="../assets/logo1.png" alt="logo" class="logo"/>
            </router-link>
          </li>
          <li class="md">
            <router-link :to="'/leetbook'">探索</router-link>
          </li>
          <li>
            <router-link :to="'/problemset/all'"> 题库</router-link>
          </li>
          <li class="md">
            <router-link :to="'/contest'"> 竞赛</router-link>
          </li>
          <li class="lg">
            <router-link :to="'/interview'"> 企业</router-link>
          </li>
          <li class="lg">
            <Dropdown class="shopping-menu">
              <a :style="{ color: 'orange' }">
                <Icon type="md-basket"/>
                商城
              </a>
              <DropdownMenu slot="list">
                <DropdownItem>精品商城</DropdownItem>
                <DropdownItem>石大周边</DropdownItem>
                <DropdownItem>Plus会员</DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </li>
        </ul>
        <Button type="success" @click="$router.push({ name: 'App' })" ghost>
          <Icon type="md-phone-portrait"/>
          赞助
        </Button>
        <Button class="lg" type="warning" ghost>Plus会员</Button>
        <ul class="nav" right>
          <li class="lg">
            <Dropdown
            ><a>
              语言
              <Icon type="ios-arrow-down"></Icon
              >
            </a>
              <DropdownMenu slot="list">
                <DropdownItem>题目以中文显示</DropdownItem>
                <DropdownItem>题目以英文显示</DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </li>
          <template v-if="username!=null">
            <a>
              <Icon type="md-notifications-outline"/>
            </a>
            <Dropdown>
              <a>
                <Icon type="md-person"/>
                欢迎你 {{ username }}
              </a>
              <template v-slot:list>
                <DropdownMenu>
                  <DropdownItem @click.native="logout">登出</DropdownItem>
                </DropdownMenu>
              </template>
            </Dropdown>
          </template>
          <template v-else>
            <a @click="dialogVisible=true">登录</a>
            <span>或</span>
            <a @click="dialogVisible=true">注册</a>
          </template>
        </ul>
      </div>
      <el-dialog
          append-to-body="append-to-body"
          title="注册/登录"
          :visible.sync="dialogVisible"
          width="700px">
        <h1>
          {{ ["欢迎来到UPCode", "账号密码登录", "注册"][loginDialogState] }}
        </h1>

        <Form v-if="loginDialogState === 0">

          <FormItem>
            <i-input placeholder="输入UPCode账户" v-model="formLogin.username"></i-input>
          </FormItem>

          <FormItem>
            <i-input type="password" placeholder="请输入密码" v-model="formLogin.password" ></i-input>
          </FormItem>
          <FormItem>
            <Button type="success" @click="handleLogin"
                    v-bind:disabled="formLogin.isLogin" v-text="formLogin.loginBtnTip" long></Button>
          </FormItem>
          <div class="bottom-jump-div">
            <a class="bottom-jump-href" @click="loginDialogState = 1">没有账号?前往注册</a>
          </div>
        </Form>

        <Form v-if="loginDialogState === 1">
          <FormItem>
            <i-input v-model="formRegister.username" placeholder="输入用户名"></i-input>
          </FormItem>

          <FormItem>
            <i-input
                type="password"
                placeholder="输入密码"
                v-model="formRegister.password1">
            </i-input>
          </FormItem>

          <FormItem>
            <i-input type="password" placeholder="再次输入密码" v-model="formRegister.password2"></i-input>
          </FormItem>

          <FormItem>
            <Button type="success" long @click="handleReg">注册</Button>
          </FormItem>

          <div class="bottom-jump-div">
            <a class="bottom-jump-href" @click="loginDialogState = 0">已有账号?前往登录</a>
          </div>
        </Form>

        <template v-slot:footer>
          <div class="login-footer">
            <p>登录注册即代表同意UPCode <a>用户协议</a> 和 <a>隐私协议 </a></p>
            <a>关于我们 </a>
            <a>问题反馈 </a>
            <a>帮助中心 </a>
          </div>
        </template>
      </el-dialog>
    </div>
</template>
<script>

export default {
  data() {
    return {
      dialogVisible: false,
      loginDialogState:0,
      formRegister: {
        username: "",
        password1: "",
        password2: "",
      },
      formLogin: {
        username: "",
        password: "",
        isLogin:false,
        loginBtnTip:"登录"
      },
      username:null}
  },
  name: "TheNavagator",
  components: {},
  created: async function () {
    await this.ping()
  },
  methods: {
    logout: async function () {
      await this.$axios.post("/auth/logout")
      this.username=null
    },
    ping: async function () {
      await this.$axios.get("/auth/ping", {timeout: 1000})
          .then(response => {
            if (response.status === 200) {
              this.username=response.data.username
            }
          })
    },
    handleLogin: async function ()
    {
      this.enableLoginBtn()
      await this.$axios.post("/auth/login",
          {username:this.formLogin.username,password:this.formLogin.password},
          {timeout:5000})
          .then(this.handleLoginSuccess)
          .catch(this.handleLoginFailed)
          .finally(this.disableLoginBtn)
    },
    handleLoginSuccess:function (rsp){
      if(rsp.data.status==="error"){
        this.$Message.error("账号或者密码错误")
      }else {
        this.$Message.info("登录成功")
        this.dialogVisible=false
        this.username=rsp.data.username
      }
    },
    handleLoginFailed:function (){
      this.$Message.error("系统异常...")
    },
    enableLoginBtn:function (){
      this.formLogin.loginBtnTip="正在登录..."
      this.formLogin.isLogin=true
    },
    disableLoginBtn:function (){
      this.formLogin.loginBtnTip="登录"
      this.formLogin.isLogin=false
    },
    handleReg:async function (){
      console.log(this.formRegister.username)
      console.log(this.formRegister.password1)
      console.log(this.formRegister.password2)
      if(this.formRegister.password1!==this.formRegister.password2){
        this.$Message.info('密码不一致')
        return
      }
      await this.$axios.post("/auth/reg",
          {username:this.formRegister.username,password:this.formRegister.password1},
          {timeout:3000})
          .then(rsp=>{
            if(rsp.data.status==='success'){
              this.$Message.info('注册成功')
              this.dialogVisible=false
            }else {
              this.$Message.warning('账号已存在')
            }
          })
          .catch(()=>{
            this.$Message.warning('注册失败')
          })
    }
  },
};
</script>

<style lang="scss" scoped>
.wrapper {
  width: 100%;
  min-width: 360px;
  z-index: 1000;

  .inner-wrapper {
    display: flex;
    margin: 0 auto;
    gap: 20px;
    justify-content: center;
    padding: 5px;
    align-items: center;

    .nav {
      display: flex;
      align-items: center;
      list-style: none;

      a {
        color: black;
        display: inline-block;
        padding: 0 10px;
      }
    }

    .left {
      margin-right: auto;
    }

    .right {
      margin-left: auto;
    }
  }

  box-shadow: 0 0 5px gray;

  @media screen and (max-width: 820px) {
    .lg {
      display: none !important;
    }
  }
  @media screen and (max-width: 460px) {
    .md {
      display: none !important;
    }
  }
}
.logo {
  transform: translateY(10%);
  height: 24px;
}
h1 {
  margin: 20px 0;
}
.login-footer {
  text-align: center;
  color: rgb(167, 167, 167);
  a {
    color: gray;
    transition: color 0.5s;
    &:hover {
      color: rgb(66, 199, 199);
    }
  }
  img {
    margin: 10px;
  }
}
.bottom-jump-div{
  text-align: right;
}
.bottom-jump-href{
  margin-right: 10px;
  display: inline-block;
  color: gray;
  &:hover {
    color: rgb(13, 174, 223);
  }
}
</style>
